/*  notifications.scss
    ---
    Adds a notification at the top of each USWDS
    compile. Use this file for important notifications
    and updates to the design system.

    This file should started fresh at each
    major version.

*/

/* stylelint-disable string-no-newline  */
$uswds-notifications: "\n\
--------------------------------------------------------------------\n\
\2709  USWDS Notifications\n\
--------------------------------------------------------------------\n\
2.4.0: If your component settings aren't working as expected, make\n\
sure you're importing the components settings in your Sass entry\n\
point (often styles.scss) with `@import \"uswds-theme-components\"`.\n\
A bug in 2.0 omitted that import.\n\
\n\
- We added `$theme-show-notifications: true` to general settings\n\
--------------------------------------------------------------------\n\
2.2.0: We changed the names of some settings.\n\
\n\
- $theme-navigation-width \2192  $theme-header-min-width\n\
- $theme-megamenu-logo-text-width \2192  $theme-header-logo-text-width\n\
--------------------------------------------------------------------\n\
2.0.2: We changed the names of some settings and mixins.\n\
\n\
- $theme-title-font-size \2192  $theme-display-font-size\n\
- @include title \2192  @include display\n\
- @include typeset-title \2192  @include typeset-display";

$uswds-notification-disable-message: "\n\
--------------------------------------------------------------------\n\
These are notifications from the USWDS team, not necessarily a\n\
problem with your code.\n\
\n\
Disable notifications using `$theme-show-notifications: false`\n\
in your general settings file.\n\
--------------------------------------------------------------------";
/* stylelint-enable  */

@if $theme-show-notifications {
  @warn "#{$uswds-notifications}"
    + "#{$uswds-notification-disable-message}";
}
